import { http } from '@/api/user'
import CGI from '@/constant/cgi'
// 仅在客户端使用js-cookie包
const Cookie = process.client ? require('js-cookie') : undefined
export default {
    name:'LoginIndex',
    middleware: 'notAuthenticated',
    data(){
        return{
            user:{
                username:'',
                email:'',
                password:''
            },
            // 错误提示信息
            errors:{}
        }
    },
    computed:{
        isLogin(){
            return this.$route.name === 'login'
        }
    },
    methods:{
      async onSubmit(){
          try {
            let pushPath=''
              if(this.isLogin){
                // 登录
                const { data } = await http({url:CGI.getLoginAPT, params:{user:this.user}}) 
                // console.log(data);
                // 保存用户的登录状态
                this.$store.commit('setUser',data.user)
                // 为防止刷新页面数据丢失，需要把数据持久化
                Cookie.set('user', data.user)
                pushPath='/'
              }else{
                // 注册
                 const { data } = await http({url:CGI.getRegisterAPI, params:{user:this.user}})
                 pushPath='/login'
              }
              // 跳转到指定页面
              this.$router.push(pushPath)
          } catch (error) {
            //   console.dir(error);
              console.log('请求失败',error.message);
              this.errors = error.response.data.errors
          }
        }
    }
}
